import React, { useState } from 'react';
import { Coins, Gift, Clock, Users } from 'lucide-react';
import { useTranslation } from 'react-i18next';

const ClaimCard = ({ 
  userInfo, 
  claimStatus,
  loading, 
  claimTokens, 
  canClaim,
  bindReferrer,
  formatTimeLeft
}) => {
  const { t } = useTranslation();
  const [referrerAddress, setReferrerAddress] = useState('');
  const [isBinding, setIsBinding] = useState(false);

  // 检查是否需要绑定推荐人
  const needsReferrer = !userInfo.referrer || userInfo.referrer === '0x0000000000000000000000000000000000000000';

  // 从URL参数中获取推荐人地址
  React.useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search);
    const refParam = urlParams.get('ref');
    if (refParam && needsReferrer && !referrerAddress) {
      setReferrerAddress(refParam);
    }
  }, [needsReferrer, referrerAddress]);

  const handleClaim = async () => {
    const result = await claimTokens();
    if (result.success) {
      alert(t('message.claimSuccess'));
    } else {
      alert(`${t('message.claimError')}: ${result.error}`);
    }
  };

  const handleBindReferrer = async () => {
    if (!referrerAddress) {
      alert(t('message.invalidReferrer'));
      return;
    }

    if (referrerAddress.length !== 42 || !referrerAddress.startsWith('0x')) {
      alert(t('message.invalidReferrer'));
      return;
    }

    setIsBinding(true);
    const result = await bindReferrer(referrerAddress);
    if (result.success) {
      alert(t('message.bindSuccess'));
      setReferrerAddress('');
    } else {
      alert(`${t('message.bindError')}: ${result.error}`);
    }
    setIsBinding(false);
  };

  return (
    <div className="claim-card">
      <div className="card-header">
        <Coins className="card-icon" />
        <h2>{t('claim.title')}</h2>
      </div>
      
      <div className="claim-info">
        <div className="info-row">
          <span className="label">{t('claim.tokensToReceive')}:</span>
          <span className="value">1,000 MT</span>
        </div>
       
        <div className="info-row">
          <span className="label">{t('claim.dailyClaims')}:</span>
          <span className="value">{userInfo.claimsToday || 0} / 2</span>
        </div>
        
        <div className="info-row">
          <span className="label">{t('claim.remaining')}:</span>
          <span className="value">{Math.max(0, 2 - (userInfo.claimsToday || 0))}</span>
        </div>
        
        {!claimStatus.canClaim && claimStatus.timeLeft > 0 && (
          <div className="info-row">
            <span className="label">{t('claim.timeLeft')}:</span>
            <span className="value time-left">{formatTimeLeft(claimStatus.timeLeft)}</span>
          </div>
        )}
      </div>

      {needsReferrer && (
        <div className="referrer-section">
          <div className="referrer-header">
            <Users size={20} />
            <span>{t('referrer.title')}</span>
          </div>
          <p className="referrer-tip">
            {t('referrer.tip')}
          </p>
          <div className="referrer-input-group">
            <input
              type="text"
              className="referrer-input"
              placeholder={t('referrer.placeholder')}
              value={referrerAddress}
              onChange={(e) => setReferrerAddress(e.target.value)}
            />
          </div>
          <button
            className="bind-btn"
            onClick={handleBindReferrer}
            disabled={isBinding || !referrerAddress}
          >
            {isBinding ? (
              <div className="loading-spinner small"></div>
            ) : (
              t('referrer.bind')
            )}
          </button>
          <div className="referrer-note">
            {t('referrer.note')}
          </div>
        </div>
      )}

      <button
        className={`claim-btn ${!canClaim() ? 'disabled' : ''}`}
        onClick={handleClaim}
        disabled={!canClaim() || loading.claimTokens}
      >
        {loading.claimTokens ? (
          <div className="loading-spinner"></div>
        ) : (
          <Gift size={20} />
        )}
        {loading.claimTokens ? t('claim.button.processing') : 
         !canClaim() ? (claimStatus.message || t('claim.button.disabled')) : t('claim.button')}
      </button>

      <div className="claim-stats">
        <div className="stat-item">
          <Clock size={16} />
          <span>{t('claim.interval')}</span>
        </div>
        <div className="stat-item">
          <Users size={16} />
          <span>{t('claim.referralBonus')}</span>
        </div>
      </div>
    </div>
  );
};

export default ClaimCard; 