import React from 'react';
import { AlertCircle, RefreshCw } from 'lucide-react';
import i18n from '../i18n';

class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props);
    this.state = { hasError: false, error: null, errorInfo: null };
  }

  static getDerivedStateFromError(error) {
    // 更新 state 使下一次渲染能够显示降级后的 UI
    return { hasError: true };
  }

  componentDidCatch(error, errorInfo) {
    // 你同样可以将错误日志上报给服务器
    console.error('ErrorBoundary caught an error:', error, errorInfo);
    this.setState({
      error: error,
      errorInfo: errorInfo
    });
  }

  handleRefresh = () => {
    window.location.reload();
  };

  render() {
    if (this.state.hasError) {
      return (
        <div className="error-boundary">
          <div className="error-content">
            <div className="error-icon">
              <AlertCircle size={64} />
            </div>
            <h1>{i18n.t('error.title')}</h1>
            <p>{i18n.t('error.subtitle')}</p>
            
            <div className="error-solutions">
              <h3>{i18n.t('error.solutions.title')}</h3>
              <ul>
                <li>{i18n.t('error.solutions.refresh')}</li>
                <li>{i18n.t('error.solutions.wallet')}</li>
                <li>{i18n.t('error.solutions.network')}</li>
                <li>{i18n.t('error.solutions.browser')}</li>
              </ul>
            </div>

            <button className="refresh-btn" onClick={this.handleRefresh}>
              <RefreshCw size={20} />
              {i18n.t('error.refresh')}
            </button>

            {process.env.NODE_ENV === 'development' && (
              <details className="error-details">
                <summary>{i18n.t('error.details')}</summary>
                <pre>{this.state.error && this.state.error.toString()}</pre>
                <pre>{this.state.errorInfo.componentStack}</pre>
              </details>
            )}
          </div>
        </div>
      );
    }

    return this.props.children;
  }
}

export default ErrorBoundary; 