import React from 'react';
import { TrendingUp, Banknote, Coins } from 'lucide-react';
import { useTranslation } from 'react-i18next';

const RewardsCard = ({ 
  userInfo, 
  loading, 
  claimEthRewards, 
  claimTokenRewards 
}) => {
  const { t } = useTranslation();
  
  const handleClaimEth = async () => {
    const result = await claimEthRewards();
    if (result.success) {
      alert(t('message.claimSuccess'));
    } else {
      alert(`${t('message.claimError')}: ${result.error}`);
    }
  };

  const handleClaimTokens = async () => {
    const result = await claimTokenRewards();
    if (result.success) {
      alert(t('message.claimSuccess'));
    } else {
      alert(`${t('message.claimError')}: ${result.error}`);
    }
  };

  return (
    <div className="rewards-card">
      <div className="card-header">
        <TrendingUp className="card-icon" />
        <h2>{t('rewards.title')}</h2>
      </div>
      
      <div className="rewards-grid">
        <div className="reward-item">
          <div className="reward-icon eth-reward">
            <Banknote size={24} />
          </div>
          <div className="reward-info">
            <h3>{t('rewards.direct')}</h3>
            <p className="reward-amount">
              {Number(userInfo.pendingEthRewards).toFixed(6)} ETH
            </p>
            <p className="total-earned">
              {t('rewards.totalEarned')}: {Number(userInfo.totalEthEarned).toFixed(6)} ETH
            </p>
          </div>
          <button
            className={`reward-claim-btn ${Number(userInfo.pendingEthRewards) <= 0 ? 'disabled' : ''}`}
            onClick={handleClaimEth}
            disabled={Number(userInfo.pendingEthRewards) <= 0 || loading.claimEthRewards}
          >
            {loading.claimEthRewards ? (
              <div className="loading-spinner small"></div>
            ) : (
              t('rewards.claim')
            )}
          </button>
        </div>

        <div className="reward-item">
          <div className="reward-icon token-reward">
            <Coins size={24} />
          </div>
          <div className="reward-info">
            <h3>{t('rewards.indirect')}</h3>
            <p className="reward-amount">
              {Number(userInfo.pendingTokenRewards).toFixed(2)} MT
            </p>
            <p className="total-earned">
              {t('rewards.totalEarned')}: {Number(userInfo.totalTokensEarned).toFixed(2)} MT
            </p>
          </div>
          <button
            className={`reward-claim-btn ${Number(userInfo.pendingTokenRewards) <= 0 ? 'disabled' : ''}`}
            onClick={handleClaimTokens}
            disabled={Number(userInfo.pendingTokenRewards) <= 0 || loading.claimTokenRewards}
          >
            {loading.claimTokenRewards ? (
              <div className="loading-spinner small"></div>
            ) : (
              t('rewards.claim')
            )}
          </button>
        </div>
      </div>

      <div className="reward-stats">
        <div className="stat-card">
          <span className="stat-label">{t('stats.yourReferrals')}</span>
          <span className="stat-value">{userInfo.referralCount}</span>
        </div>
        <div className="stat-card">
          <span className="stat-label">{t('stats.directBonus')}</span>
          <span className="stat-value">10% ETH + 20% MT</span>
        </div>
        <div className="stat-card">
          <span className="stat-label">{t('stats.indirectBonus')}</span>
          <span className="stat-value">2% ETH + 10% MT</span>
        </div>
      </div>
    </div>
  );
};

export default RewardsCard; 