import React, { useEffect, useState } from 'react';
import './App.css';
import Header from './components/Header';
import ClaimCard from './components/ClaimCard';
import RewardsCard from './components/RewardsCard';
import StatsCard from './components/StatsCard';
import ErrorBoundary from './components/ErrorBoundary';
import { useContract } from './hooks/useContract';
import { useContractOperations } from './hooks/useContractOperations';
import { Loader2, AlertCircle, Wifi } from 'lucide-react';
import { useTranslation } from 'react-i18next';

function App() {
  const { t } = useTranslation();
  const {
    contract,
    account,
    isConnected,
    loading: contractLoading,
    connectWallet,
    disconnectWallet
  } = useContract();

  const {
    userInfo,
    claimStatus,
    loading,
    claimTokens,
    claimEthRewards,
    claimTokenRewards,
    bindReferrer,
    fetchUserInfo,
    formatTimeLeft,
    canClaim
  } = useContractOperations(contract, account);

  const [networkStatus, setNetworkStatus] = useState('online');

  // 检查网络状态
  useEffect(() => {
    const handleOnline = () => setNetworkStatus('online');
    const handleOffline = () => setNetworkStatus('offline');

    window.addEventListener('online', handleOnline);
    window.addEventListener('offline', handleOffline);

    return () => {
      window.removeEventListener('online', handleOnline);
      window.removeEventListener('offline', handleOffline);
    };
  }, []);

  // 处理URL中的推荐参数
  useEffect(() => {
    const urlParams = new URLSearchParams(window.location.search);
    const ref = urlParams.get('ref');
    if (ref && ref !== account) {
      localStorage.setItem('referrer', ref);
    }
  }, [account]);

  if (networkStatus === 'offline') {
    return (
      <div className="app offline-mode">
        <div className="offline-notice">
          <Wifi size={48} />
          <h2>{t('message.networkError')}</h2>
          <p>{t('message.networkError')}</p>
        </div>
      </div>
    );
  }

  return (
    <ErrorBoundary>
      <div className="app">
        <div className="background-animation">
          <div className="gradient-orb orb-1"></div>
          <div className="gradient-orb orb-2"></div>
          <div className="gradient-orb orb-3"></div>
        </div>

      <Header
        isConnected={isConnected}
        account={account}
        connectWallet={connectWallet}
        disconnectWallet={disconnectWallet}
        loading={contractLoading}
      />

      <main className="main-content">
        {!isConnected ? (
          <div className="welcome-section">
            <div className="welcome-card">
              <div className="welcome-header">
                <div className="welcome-icon">
                  <div className="icon-bg">
                    <Loader2 className="rotating-icon" size={32} />
                  </div>
                </div>
                <h1>{t('welcome.title')}</h1>
                <p>{t('welcome.subtitle')}</p>
              </div>
              
              <div className="features-grid">
                <div className="feature-item">
                  <div className="feature-icon">🎁</div>
                  <h3>{t('welcome.features.token.title')}</h3>
                  <p>{t('welcome.features.token.desc')}</p>
                </div>
                <div className="feature-item">
                  <div className="feature-icon">🚀</div>
                  <h3>{t('welcome.features.referral.title')}</h3>
                  <p>{t('welcome.features.referral.desc')}</p>
                </div>
                <div className="feature-item">
                  <div className="feature-icon">⚡</div>
                  <h3>{t('welcome.features.instant.title')}</h3>
                  <p>{t('welcome.features.instant.desc')}</p>
                </div>
                <div className="feature-item">
                  <div className="feature-icon">🔒</div>
                  <h3>Security</h3>
                  <p>Smart contract security</p>
                </div>
              </div>

              <button 
                className="get-started-btn"
                onClick={connectWallet}
                disabled={contractLoading}
              >
                {contractLoading ? (
                  <>
                    <Loader2 className="spinning" size={20} />
                    {t('loading.connecting')}
                  </>
                ) : (
                  t('wallet.getStarted')
                )}
              </button>
            </div>
          </div>
        ) : (
          <>
            {loading.fetchingData ? (
              <div className="loading-overlay">
                <Loader2 className="spinning" size={48} />
                <p>{t('loading.connecting')}</p>
              </div>
            ) : (
              <div className="dashboard">
                <div className="cards-grid">
                  <ClaimCard
                    userInfo={userInfo}
                    claimStatus={claimStatus}
                    loading={loading}
                    claimTokens={claimTokens}
                    bindReferrer={bindReferrer}
                    formatTimeLeft={formatTimeLeft}
                    canClaim={canClaim}
                  />
                  
                  <RewardsCard
                    userInfo={userInfo}
                    loading={loading}
                    claimEthRewards={claimEthRewards}
                    claimTokenRewards={claimTokenRewards}
                  />
                  
                  <StatsCard
                    userInfo={userInfo}
                    account={account}
                  />
                </div>
              </div>
            )}
          </>
        )}
      </main>

      <footer className="footer">
        <div className="footer-content">
          <p>{t('footer.rights')}</p>
          <div className="social-links">
            <a href="#" className="social-link">{t('footer.discord')}</a>
            <a href="#" className="social-link">{t('footer.twitter')}</a>
            <a href="#" className="social-link">{t('footer.telegram')}</a>
          </div>
        </div>
      </footer>
    </div>
    </ErrorBoundary>
  );
}

export default App;