import React from 'react';
import { BarChart3, Share2, Copy, Check } from 'lucide-react';
import { useState } from 'react';
import { useTranslation } from 'react-i18next';

const StatsCard = ({ userInfo, account }) => {
  const { t } = useTranslation();
  const [copied, setCopied] = useState(false);

  const referralLink = `${window.location.origin}?ref=${account}`;

  const copyReferralLink = async () => {
    try {
      await navigator.clipboard.writeText(referralLink);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    } catch (err) {
      // 如果clipboard API不可用，使用传统方法
      const textArea = document.createElement('textarea');
      textArea.value = referralLink;
      document.body.appendChild(textArea);
      textArea.select();
      document.execCommand('copy');
      document.body.removeChild(textArea);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    }
  };

  return (
    <div className="stats-card">
      <div className="card-header">
        <BarChart3 className="card-icon" />
        <h2>{t('stats.title')}</h2>
      </div>
      
      <div className="stats-grid">
        <div className="stat-item">
          <div className="stat-number">{userInfo.claimsToday}</div>
          <div className="stat-label">{t('stats.totalClaimed')}</div>
        </div>
        <div className="stat-item">
          <div className="stat-number">{userInfo.referralCount}</div>
          <div className="stat-label">{t('stats.yourReferrals')}</div>
        </div>
        <div className="stat-item">
          <div className="stat-number">
            {Number(userInfo.totalEthEarned).toFixed(4)}
          </div>
          <div className="stat-label">Total ETH</div>
        </div>
        <div className="stat-item">
          <div className="stat-number">
            {Number(userInfo.totalTokensEarned).toFixed(0)}
          </div>
          <div className="stat-label">Total MT</div>
        </div>
      </div>

      {userInfo.referrer && (
        <div className="referrer-info">
          <div className="referrer-label">{t('stats.yourReferrer')}:</div>
          <div className="referrer-address">
            {`${userInfo.referrer.slice(0, 6)}...${userInfo.referrer.slice(-4)}`}
          </div>
        </div>
      )}

      <div className="referral-section">
        <div className="referral-header">
          <Share2 size={20} />
          <span>{t('referral.title')}</span>
        </div>
        <div className="referral-link-container">
          <input
            type="text"
            className="referral-link-input"
            value={referralLink}
            readOnly
          />
          <button
            className={`copy-btn ${copied ? 'copied' : ''}`}
            onClick={copyReferralLink}
          >
            {copied ? <Check size={16} /> : <Copy size={16} />}
            {copied ? t('referral.copied') : t('referral.copy')}
          </button>
        </div>
        <div className="referral-tip">
          {t('referral.tip')}
        </div>
      </div>
    </div>
  );
};

export default StatsCard; 